  <div class="home">
    <div v-if="path.indexOf($route.path) > -1 ">
<!--     
      <span class="title">社区</span> -->
      <div class="nav_one">
        <!-- <div class="nav_one_left ">
          <span class="select" @click="oneSelsectClick(1)" :class="oneIndex == 1?'oneSelsect':''">E区</span>
          <span class="select" @click="oneSelsectClick(2)" :class="oneIndex == 2?'oneSelsect':''">最新</span>
          <span class="select" @click="oneSelsectClick(3)" :class="oneIndex == 3?'oneSelsect':''">热门</span>
        </div> 
        <div class="nav_right">
          <img src="../assets/images/release.png" class="nav_right_img" alt="">
          <span class="nav_right_text" @click="handleBtn()">发布动态</span>
        </div>
        -->
      </div>
      <div class="nav_two">
        <div class="nav_one_left">
          <router-link to="/square/index">
            <span @click="twoSelsectClick(1)" :class="twoIndex == 1?'twoSelsect':'defaultColor'">广场</span>
          </router-link>
          <router-link to="/answers/index?index=1">
            <span @click="twoSelsectClick(2)" :class="twoIndex == 2?'twoSelsect':'defaultColor'">问答</span>
          </router-link>
          <router-link to="/specialtopic/index">
            <span @click="twoSelsectClick(3)" :class="twoIndex == 3?'twoSelsect':'defaultColor'">专题</span>
          </router-link>          
          
          <router-link to="/my/index/square">
            <span @click="twoSelsectClick(1)" :class="twoIndex == 4?'twoSelsect':'defaultColor'">我的</span>
          </router-link>
        </div>
          <div class="nav_right">
          <img src="../assets/images/release.png" class="nav_right_img" alt="">
          <span class="nav_right_text" @click="handleBtn()">发布动态</span>
        </div>
      </div>
    </div>
    <van-action-sheet v-model="show" :actions="actions" @select="onSelect" />
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'home',
  props: {
    msg: String
  },
  data() {
    return {
      show: false,
      path:['/square/index','/answers/index','/specialtopic/index'],
      actions:[],
      oneIndex:1,
      twoIndex:1
    }
  },
  created(){
    console.log(window.location.href);
  //this.$store.dispatch('getToken')
    this.$store.dispatch('registerTeld')
   
  },
  methods: {
    handleBtn(){
      if(!this.$store.state.userData){
          window.location.href= "teldapp://login"
          return null;
      }
      let data = {
        token:this.$store.state.userData,
        type:this.$store.state.oneIndex
      }
       this.api.isCanAddThread(data).then(res=> {
        
            if(res.err_code == 0) {
              this.$store.commit('isAdmin',res.data.is_admin)
              if(res.data.status == 0) {
                this.$toast('没有发布权限');
                return false
              }
            }
        })
      switch(this.twoIndex){
        case 1:
            this.actions = [{ name: '添加图片' },{ name: '添加视频' },{name:''},{name:''}]
            this.show = true;
          break;
        case 2:
           this.$router.push('/answers/add')
      }
      
    },
    onSelect(v){
      this.show = false;
      switch(v.name){
        case '添加图片':
            this.$router.push({path:'/square/add',query:{type:'image'}})
          break
        case '添加视频':
            this.$router.push({path:'/square/add',query:{type:'video'}})
          break  
      }
    
    },
    //一级导航
    oneSelsectClick(i) {
      this.oneIndex = i
    },
    twoSelsectClick(i) {
      this.$store.commit('oneIndex',i)
      this.twoIndex = i
      // switch(i){
      //   case 1:
      //       let url = `${this.$store.state.indexUrl+'my/index/square?'+this.$store.state.appParams}`,
      //           json = {url:url,share:'0'};
      //       if(!this.$store.state.userData){
      //           window.location.href= "teldapp://login"
      //       }else {
      //           window.location.href= `teldapp://newWebview?params=`+JSON.stringify(json)
      //       }
      //     break;
      // } 
    }
  },
   beforeRouteEnter (to, from, next) {
     
    next((v) => {
      switch(to.name){
        case 'answers':
            v.twoIndex = 2;
          break;
      }
    });
  },
  beforeRouteUpdate (to, from, next) {
 
    switch(to.name){
      case 'square':
          this.twoIndex = 1;
        break;
      case 'answers':
          this.twoIndex = 2;
        break; 
      case 'specialtopic':
          this.twoIndex = 3;
        break;    
    }
    //console.log(this.twoIndex);
    next();
  }

}
</script>

